.navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    color: antiquewhite;
    background-color: #8d8e94;
    /* cursor: pointer; */
    width: 100%;
    
    /* border: 1px solid red; */
    gap: 2rem;
    /* margin: auto; */
    /* margin-left: 50%; */
    /* padding-top: 1rem; */
    cursor: pointer;
    
    position: fixed;
    top: 0;
   z-index: 2;
}


.help{
  width: 12%;
  /* height: 20px; */
  border-radius: 5%;
  box-sizing: border-box;
  padding: 1.3%;
  /* margin-top: -1rem; */
  font:bold;
  font-size: large;
  font-weight: 600;
  
  color: aqua;
  /* border: 1px solid red; */
}

.help:hover {
    /* transform: scale(1.2); */
    /* transition: all 0.9s ease; */
    /* text-decoration: underline; */
    background-color: aqua;
    /* font:bolder; */
    
    
    color: black;
  }



  



.main_nav>.hamburger_menu{
    
    display: none;
  }



  @media(max-width:920px){
     
    .navbar{
        display: none;
    }
     .mobile_view{
        grid-column: 2/4;
        position: relative;
        left: 60%;
        top: 20%;
        z-index: 99;
    }

    .mobile_view{

    /* background-color: white; */
    /* border: 1px solid red; */
    margin-top: 5rem;
    height: 15rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    justify-content: center;
    /* padding-left: 3rem; */
    padding: 3rem;
    padding-top: 2rem;
  
    transition: all 2s linear;
    transform-origin: top;
    /* border: 1px solid red; */
    width: 20%;
    color: antiquewhite;
    background-color: gray;
    border-bottom-left-radius: 5%;
    border-top-left-radius: 5%;
    /* box-shadow: rgba(98, 155, 192, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px; */
    }


    .help{
      width: 30%;
      font-size: 120%;
      padding: 0.5rem;
      /* border: 1px solid red; */
      /* background-color: gray; */
      /* margin-right: 20%; */
      text-align: center;
      cursor: pointer;
      margin: auto;
    }

    .help:hover{
      background-color: aqua;
    }

     .main_nav >.hamburger_menu{
        display: block;
        font-size: 30px;
        width: 10%;
        margin-top: 10%;
        position: absolute;
        right: 20%;
        /* margin-left: 90%; */
    }


  }

